export default {
	data() {
		return {
			/* 设置 config */
			editType: 'font',
			isShow: true,
			config:{
				img:{
					r: 0,
				},
				font:{
					lineHeight: 1,
					size: 1,
					weight:false,
					lineThrough:false,
					color:'#000'
				}
			}			
			// inputContent: '',
		}
	},
	methods: {
		/* 设置 config */
		selectEditType(type) { //选择编辑方式 select type of edit
			this.editType = type
		},
		close() {
			this.isShow = false
		},
		info() {
			uni.showModal({
				title: '提示',
				content: '这是一个模态弹窗',
				showCancel: false,
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
					}
				}
			});
		},
		unfold() {
			this.isShow = true
		},
		handleItemCenter(){
			this.$emit('handlePosition',this.editType,'centerH')
		},
		handlLineHeight(num) {
			if (this.config.font.lineHeight + num < 0) return
			this.config.font.lineHeight = this.config.font.lineHeight + num
			this.$emit('handleConfig',this.editType,this.config)
		},
		handlFontSize(num) {
			if (this.config.font.size + num < 0) return
			this.config.font.size = this.config.font.size + num
			this.$emit('handleConfig',this.editType,this.config)
		},
		handlRadius(num) {
			if (this.config.img.r + num < 0) return
			this.config.img.r = this.config.img.r + num
			this.$emit('handleConfig',this.editType,this.config)
		},
		handleWeight(){
			this.config.font.weight = !this.config.font.weight
			this.$emit('handleConfig',this.editType,this.config)
		},
		handleLineThrough(){
			this.config.font.lineThrough = !this.config.font.lineThrough
			this.$emit('handleConfig',this.editType,this.config)
		},
		handleColor(){
			
		},
	}
}
